#abb select{
  appearance:none;
  -webkit-appearance: none;
-moz-appearance: none;
padding: 0px 10px;
width: 100%;  
background-color: #ffffff;
}
#abb{
  position: relative;
  max-width: 100%;
}
#abb select , #abb::after{
  height: 38px;
  padding: 0px 25px;
  border: none;
}
#abb::after{
position: absolute;
top: 0px;
right: 0px;
content:"\25bc";
line-height: 40px;
padding: 0px 15px;
}
